import React, { Component } from 'react';
import styled from "styled-components";

const AssistTitleStyle = styled.div `
    width: 100%;
    height: 100%;
    color: #black;
    font-size: 25px;
    text-decoration: none;
    transition: 1s;
    font-family: 'Do Hyeon', sans-serif;
`;

const AssistMentStyle = styled.div`
    font-size: 15px;
    font-family: 'Noto Sans KR', sans-serif;
    color: #747474;
    line-height: 30px;

    @media (min-width:0px) and (max-width: 1023.9px) {
        font-size: 12.5px;
    }
`;

class AssistBox extends Component {  
    render() {
        return (
        <div>
            <AssistTitleStyle>
                
                <p>"   "</p>
            </AssistTitleStyle>
            <AssistMentStyle>
                <p>
                    <b>"   "</b>     .<br />
                         ,<br />
                         <br />
                      .<br />  
                    
                </p>
            </AssistMentStyle><br></br>
            <AssistMentStyle>
                <p>
                      ,<br />
                        ,<br />
                        <br />
                     
                </p>
            </AssistMentStyle>
        </div>
        )
    }
}

export default AssistBox;