import React, { Component } from 'react';
import styled from 'styled-components';
import tmp1 from '../AboutMe/img/help.png';
import tmp2 from '../AboutMe/img/monitor.png';
import tmp3 from '../AboutMe/img/wifi.png';

const VisionStyle = styled.div`
    width: 100%;
    height: 100%;
    display: flex; 
    font-size: 50px;
    padding-top: 30px;
    padding-bottom: 30px;
    margin-top: 30px;
    background-color: #ffffff;
    @media (min-width:0px) and (max-width:680.9px) {
        border-radius: 0px 0px 0px 0px;
        margin-top: 640px;
    }
`

const VisionTitleStyle = styled.div `
    width: 100%;
    font-size: 28px;
    font-weight: bold;
    color: black;
    transition: 1s;
    font-family: 'Play', sans-serif;
`;

const VisionMentStyle = styled.div `
    width: 100%;
    color: #747474;
    font-family: 'Noto Sans KR', sans-serif;
`;

const MentLocation = styled.div`
    width: 100%;

    @media (min-width:0px) and (max-width:320.9px) {
        margin-left: 50%;
        font-size: 12px;
        line-height: 25px;
    }

    @media (min-width:321px) and (max-width:680.9px) {
        margin-left: 50%;
        
        font-size: 14px;
        line-height: 25px;
    }

    @media (min-width:681px) and (max-width:767.9px) {
        margin-left: 100px;
        font-size: 14px;
        line-height: 25px;
    }

    @media (min-width:768px) and (max-width:859.9px) {

        margin-left: 100px;
        font-size: 15px;
        line-height: 25px;
    }

    @media (min-width:860px) and (max-width:1023.9px) {
        margin-left: 100px;
        font-size: 15px;
        line-height: 25px;
    }

    @media (min-width: 1024px) and (max-width: 1599.9px) {
        margin-left: 307px;
        font-size: 17px;
        line-height: 30px;
    }
    
    @media (min-width: 1600px) {
        width: 2200px;
        margin-left: 20.3%;
        font-size: 17px;
    }
`;

const WhyImg = styled.div `
    width: 55px;
    height: 55px;
    background:url(${tmp1});
    transition: 1s;
    display: block;
    margin: 0px auto;
    @media (min-width:0px) and (max-width:349.9px) {
        margin-left: -2px;
    }
    
`;

const ResponsibilityImg = styled.div `
    width: 55px;
    height: 55px;
    background:url(${tmp2});    
    transition: 1s;
    display: block;
    margin: 0px auto;
    @media (min-width:0px) and (max-width:349.9px) {
        margin-left: -2px;
    }
`;

const CommunicationImg = styled.div `
    width: 55px;
    height: 55px;
    background:url(${tmp3});
    transition: 1s;
    display: block;
    margin: 0px auto;
    @media (min-width:0px) and (max-width:349.9px) {
        margin-left: -1px;
    }
`;

const TableLocation = styled.div`
@media (min-width:0px) and (max-width: 680.9px) {
    margin-left: -59%;
}
`

const WorkStyle = styled.div`
    margin-top: 10px;
    font-size: 25px;
    color: #127da7;
    font-family: 'PT Sans Narrow', sans-serif;
    margin-bottom: 20px;
    
`

const FontSize = styled.div`
    font-size: 20px;
`

class VisionMent extends Component {
    render() {
        return (
        <div>
            <VisionStyle>
                
            <MentLocation>
            
                <TableLocation>
                <WorkStyle>&nbsp;W O R K &nbsp; S T Y L E</WorkStyle>
                <table border="0px" width="76%" height="515px" bgcolor="#ffffff">
                <tbody>
                    <tr>
                        <td>
                            <br></br>
                            <VisionTitleStyle> Why</VisionTitleStyle>
                        </td>
                    </tr>
                    <tr>    
                        <td width="80%">
                            <br></br>
                            <VisionMentStyle>
                                        .<br></br>
                                 " Why "          .<br></br>
                            </VisionMentStyle>
                        </td>
                        <td width="20%">
                            <WhyImg></WhyImg>
                        </td>   
                    </tr>

                    <tr><td><br></br></td></tr>
                    <tr>
                        <td colSpan="2">
                        <hr color="fafafa"></hr>
                        </td>
                        
                    </tr>
                    <tr><td><br></br></td></tr>

                    <tr>
                        <td>
                            <VisionTitleStyle> Responsibility</VisionTitleStyle>
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <br></br>
                            <VisionMentStyle>
                                   .<br></br>
                                             .<br></br>
                            </VisionMentStyle>
                        </td>
                        <td>
                            <ResponsibilityImg></ResponsibilityImg>
                        </td>
                    </tr>

                    <tr><td><br></br></td></tr>

                    <tr>
                        <td colSpan="2">
                            <hr color="fafafa"></hr>
                        </td>
                    </tr>

                    <tr><td><br></br></td></tr>

                    <tr>
                        <td>
                            <VisionTitleStyle> Communication</VisionTitleStyle>
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <br></br>
                            <VisionMentStyle>
                                       .<br></br>
                                     ,         .<br></br>                          
                            </VisionMentStyle>
                        </td>
                        <td>
                            <CommunicationImg></CommunicationImg>
                        </td>
                    </tr>

                    <tr><td><br></br></td></tr>
                </tbody>
                </table>
                </TableLocation>
                </MentLocation>
                {/* <MentLocation>
                    <VisionTitleStyle>Why</VisionTitleStyle>
                    <VisionMentStyle>
                                .<br></br>
                         "Why"          .
                    </VisionMentStyle>

                    
                    <VisionMentStyle>
                           .<br></br>
                                      .  
                    </VisionMentStyle>   

                    
                    <VisionMentStyle>
                        
                    </VisionMentStyle>
                </MentLocation>
                <Tmp>asdasds</Tmp>
                <ImgLocation>
                        <WhyImg></WhyImg>
                        <ResponsibilityImg></ResponsibilityImg>
                        <CommunicationImg></CommunicationImg>
                </ImgLocation> */}
            </VisionStyle>
        </div>
        );
    }
}

export default VisionMent;